import "../scss/main.scss";
import { default as a } from "./alert";
import { error, success } from "./notify";

export function alert(alertMessage, event) {
  a(alertMessage, event);
}

export const notify = {
  success: (text, position, timeout) => {
    success(text, position, timeout);
  },
  error: (text, position, timeout) => {
    error(text, position, timeout);
  },
};
