import elementFactory from "./helpers/elementFactory";

function setNotifier(text, position = "top left", timeout = 2000, state) {
  let directions = position.split(" ");
  let initialPos = "";
  let openingPos = "";

  switch (position) {
    case "top left":
      initialPos = "translate(-100%, 0)";
      openingPos = "translate(15px, 15px)";
      break;
    case "bottom left":
      initialPos = "translate(-100%, 0)";
      openingPos = "translate(15px, -15px)";
      break;
    case "top right":
      initialPos = "translate(100%, 0)";
      openingPos = "translate(-15px, 15px)";
      break;
    case "bottom right":
      initialPos = "translate(100%, 0)";
      openingPos = "translate(-15px, -15px)";
      break;
    case "top center":
      initialPos = "translate(0, -100%)";
      openingPos = "translate(0, 15px)";
      break;
    case "bottom center":
      initialPos = "translate(0, 100%)";
      openingPos = "translate(0, -15px)";
      break;

    default:
      break;
  }

  let wrapperElement = document.querySelector(
    `.mf-notifier-wrapper.${directions[0] + "." + directions[1]}`
  );

  if (!wrapperElement)
    wrapperElement = elementFactory({
      name: "div",
      classes: ["mf-notifier-wrapper", directions[0], directions[1]],
      appendTo: document.body,
    });

  const notifierElement = elementFactory({
    name: "div",
    classes: [
      "mf-notifier",
      "open",
      `${state ? "success" : "error"}`,
      directions[0],
      directions[1],
    ],
    innerHtml: `${text}<div class="mf-timeout-indicator" style="animation-duration:${timeout}ms;"></div>`,
    styles: [
      {
        property: "transform",
        value: initialPos,
      },
    ],
    appendTo: directions[0] === "top" ? undefined : wrapperElement,
    insertBefore:
      directions[0] === "top"
        ? { parent: wrapperElement, child: wrapperElement.children[0] }
        : undefined,
  });

  setTimeout(() => {
    notifierElement.style.transform = openingPos;
  }, 50);

  setTimeout(() => {
    notifierElement.className = "mf-notifier";
    notifierElement.style.transform = initialPos;
  }, timeout);
  setTimeout(() => wrapperElement.removeChild(notifierElement), timeout + 600);
}

export const success = (text, position, timeout) =>
  setNotifier(text, position, timeout, true);
export const error = (text, position, timeout) =>
  setNotifier(text, position, timeout, false);
