function elementFactory({
  name,
  classes,
  text,
  styles,
  appendTo,
  children,
  event,
  innerHtml,
  attributes,
  insertBefore,
}) {
  const el = document.createElement(name);

  if (classes) classes.forEach((c) => el.classList.add(c));

  if (styles) styles.forEach((s) => (el.style[s.property] = s.value));

  if (innerHtml) el.innerHTML = innerHtml;

  if (attributes) attributes.forEach((a) => el.setAttribute(a.name, a.value));

  if (text) {
    const textNode = document.createTextNode(text);
    el.appendChild(textNode);
  }

  if (children) {
    children.forEach((c) => el.appendChild(c));
  }

  if (event) el.addEventListener(event[0], event[1]);

  if (insertBefore) insertBefore.parent.insertBefore(el, insertBefore.child);

  if (appendTo) {
    appendTo.appendChild(el);
  }

  return el;
}

export default elementFactory;
