import elementFactory from "./helpers/elementFactory";
import { times, check, questionMark } from "./icons";

const alertElem = elementFactory({
  name: "div",
  classes: ["mf-alert"],
  appendTo: document.body,
  event: [
    "click",
    function (e) {
      if (e.target === this) closeAlert();
    },
  ],
});

const successBtn = elementFactory({
  name: "div",
  classes: ["mf-btn", "success", "mr-1"],
  innerHtml: check + " Tamam",
  event: ["click", closeAlert],
});

const cancelBtn = elementFactory({
  name: "div",
  classes: ["mf-btn", "danger"],
  innerHtml: times + " İptal",
  event: ["click", closeAlert],
});

const btnWrapper = elementFactory({
  name: "div",
  classes: ["mf-btn-wrapper"],
  children: [successBtn, cancelBtn],
});

const innerElem = elementFactory({
  name: "div",
  classes: ["mf-alert-inner"],
  appendTo: alertElem,
  innerHtml: `<div class="mf-icon-wrapper">${questionMark}</div>
             <p class="mf-alert-message">Silme işlemini gerçekleştirmek istiyor musunuz?</p>`,
  children: [btnWrapper],
});

function alert(alertMessage, event) {
  alertElem.classList.add("open");

  innerElem.querySelector(".mf-alert-message").innerText = alertMessage;

  innerElem.querySelector(".mf-btn.success").onclick = event;
}

function closeAlert() {
  alertElem.classList.remove("open");
}

export default alert;
